import React, { Component } from 'react';
import Loader from 'react-loader-spinner';
import SweetAlert from 'react-bootstrap-sweetalert';
class Subscribe extends Component {

    constructor(props) {
        super(props);

        this.state = {
            fname: '',
            email: '',
            phone: '',
            err: '',
            loading:false,
        };

    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value , err: '' });
        
    }

    onRegister= () => {
        const {  email } = this.state; 
        const url = 'https://api.venture-consult.com/subscription';   // site that doesn’t send Access-Control-*
    
        fetch(url, {
            method: 'POST',
            headers:
            {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Credentials': 'true',
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              
                email,
            })

        })
            .then((response) => response.json()).then((data) => {
               // console.log("RESP",data);
                if ( !!data.success ) {
                    const getAlert = () => (
                        <SweetAlert
                        success
                       
                        confirmBtnText="ok"
                      
                        title="You have successfully subscribed to the newsletter."
                        onConfirm={() => {this.setState({alert:null})}}
                        
                       
                       >
                         Thank your for your subscription.
                      </SweetAlert>
                    );
                    this.setState({
                        loading:false,
                        err:'',
                        alert: getAlert()
                    });

                }else  {
                    const getAlert = () => (
                        <SweetAlert
                        success
                       
                        confirmBtnText="ok"
                      
                        title="You have successfully subscribed to the newsletter."
                        onConfirm={() => {this.setState({alert:null})}}
                        
                       
                       >
                        Thank your for your subscription.
                      </SweetAlert>
                    );
                    
                    this.setState({
                    err: 'Please try again',
                    loading:false,
                    alert:getAlert()
                });
            }
                

            }).catch((e) => {
                const getAlert = () => (
                    <SweetAlert
                    warning
                   
                    confirmBtnText="ok"
                  
                    title="Please try Again!"
                    onConfirm={() => {this.setState({alert:null})}}
                    
                   
                   >
                    
                  </SweetAlert>
                );
                this.setState({
                    err: 'Please try again',
                    loading:false,
                    alert: getAlert(),
                });
                console.log(e);
            });

    }

    onSubmit(e) {

        e.preventDefault();
        
      
        
        this.setState({loading:true},this.onRegister());

    }
    renderErr = () => {
        if (this.state.err === '') return null;
        return (
            <h6 className="error" > {this.state.msg} </h6>
        );
    }
    renderButton = () => {
        if (this.state.loading) {
            return (<Loader type="BallTriangle" color="blue" height={40} width={40} />)
        }
        return ( <button className="btn btn-primary" type="submit">Subscribe</button>);
    }
    render() {
       // console.log("STATE",this.state.alert);
        
        return (
            <section className="subscribe-area ptb-100">
                <div className="container">
                    <div className="section-title">
                        <h2>Connect With Us</h2>
                        <div className="bar"></div>
                       <p> Always stay in touch with our latest</p>
                    </div>
                    { this.renderErr() }
                    {this.state.alert}

                    <form className="newsletter-form" data-toggle="validator" onSubmit={this.onSubmit.bind(this)}>
                        <input type="email" className="form-control" placeholder="Your Email Address" name="email" value={this.state.value} required={true} autoComplete="off" onChange={this.onChange.bind(this)}/>
                        {this.renderButton()}
                        <div id="validator-newsletter" className="form-result"></div>
                    </form>

                </div>
            </section>
        );
    }
}

export default Subscribe;
