import React, { Component } from 'react';
import Loader from 'react-loader-spinner';
import SweetAlert from 'react-bootstrap-sweetalert';

class Form extends Component {

    constructor(props) {
        super(props);

        this.state = {
            fname: '',
            email: '',
            phone: '',
            err: '',
            loading:false,
        };

    }
    onChange(e) {
        this.setState({ [e.target.name]: e.target.value , err: '' });
        
    }

    onRegister= () => {
        const { fname , phone , email } = this.state; 
        const url = 'https://api.venture-consult.com/register_to_course';   // site that doesn’t send Access-Control-*
    
        fetch(url, {
            method: 'POST',
            headers:
            {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Credentials': 'true',
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                name: fname,
                email,
                phone_number:phone,
                course_id: this.props.id
            })

        })
            .then((response) => response.json()).then((data) => {
                console.log("RESP",data);
                if ( !!data.success ) {
                    const getAlert = () => (
                        <SweetAlert
                        success
                       
                        confirmBtnText="ok"
                      
                        title="You have successfully registered to the course."
                        onConfirm={() => {this.setState({alert:null})}}
                        
                       
                       >
                         
                      </SweetAlert>
                    );
                    this.setState({
                        loading:false,
                        err:'',
                        alert: getAlert()
                    });
                    

                }else {
                    const getAlert = () => (
                        <SweetAlert
                        danger
                       
                        confirmBtnText="ok"
                      
                        title={data.message}
                        onConfirm={() => {this.setState({alert:null})}}
                        
                       
                       >
                         
                      </SweetAlert>
                    );
                    this.setState({
                        loading:false,
                        err:'Please try again',
                        alert: getAlert()
                    });
                } 
                

            }).catch((e) => {
                const getAlert = () => (
                    <SweetAlert
                    danger
                   
                    confirmBtnText="ok"
                  
                    title='Please try again'
                    onConfirm={() => {this.setState({alert:null})}}
                    
                   
                   >
                     
                  </SweetAlert>
                );
                this.setState({
                    loading:false,
                    err:'Please try again',
                    alert: getAlert()
                });
                console.log(e);
            });

    }

    onSubmit(e) {

        e.preventDefault();
        
        this.setState({loading:true},this.onRegister());

    }
    renderErr = () => {
        if (this.state.err === '') return null;
        return (
            <h6 className="error"> {this.state.msg} </h6>
        );
    }
    renderButton = () => {
        if (this.state.loading) {
            return (<Loader type="BallTriangle" color="blue" height={40} width={40} />)
        }
        return ( <button type="submit" className="btn btn-primary">Register</button>);
    }

    render() {
       
        return (
            <section className="contact-area ptb-100">
                <div className="container">
                    <div className="section-title">
                        <h2>Register for course</h2>
                        <div className="bar"></div>
                    </div>

                    <div className="row h-100 justify-content-center align-items-center">


                        <div className="col-lg-6 col-md-12">
                            <form id="contactForm" onSubmit={this.onSubmit.bind(this)}>
                                <div className="row">
                                    <div className="col-lg-12 col-md-12">
                                        <div className="form-group">
                                            <input type="text"
                                                className="form-control"
                                                placeholder="Full name"
                                                value={this.state.value}
                                                name="fname"
                                                required={true}
                                                onChange={this.onChange.bind(this)}
                                            />
                                            <div className="help-block with-errors"></div>
                                        </div>
                                    </div>

                                    <div className="col-lg-12 col-md-12">
                                        <div className="form-group">
                                            <input type="email"
                                                className="form-control"
                                                placeholder="Email"
                                                value={this.state.value}
                                                name="email"
                                                required={true}
                                                onChange={this.onChange.bind(this)}
                                            />
                                            <div className="help-block with-errors"></div>
                                        </div>
                                    </div>

                                    <div className="col-lg-12 col-md-6">
                                        <div className="form-group">
                                            <input type="text"
                                                className="form-control"
                                                placeholder="Phone Number"
                                                value={this.state.value}
                                                name="phone"
                                                required={true}
                                                onChange={this.onChange.bind(this)}
                                            />
                                        </div>
                                    </div>
                                    { this.renderErr() }
                                    {this.state.alert}
                                    <div className="col-lg-12 col-md-12">
                                        {this.renderButton()}
                                       
                                        <div id="msgSubmit" className="h3 text-center hidden"></div>
                                        <div className="clearfix"></div>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default Form;
