import React, { Component } from 'react';

import SweetAlert from 'react-bootstrap-sweetalert';

class TeamMemberBody extends Component {

    constructor(props) {
        super(props);
        this.state={};
    }


    getinit = (id) => {
          
        const { course } = this.props;
        this.setState({
            course
        })

    }
    
    componentDidMount() {
        
        const { id } = this.props;
        
        this.getinit(id);
    
     
    }

    
    render() {
        
        //console.log("STATE",this.props);
        const { member } = this.props;
        
        return (
            <section className="contact-info-area loader ptb-100" >
                <div className="container" >
                    <div className="row h-100 justify-content-center align-items-center">
                        <div className="col-lg-8">
                        <img src={`${member.logo}`} alt="team" style={{width:null,height:280}} />
                        <p style={{marginTop:22}}>{ member.certificates} </p>
                        <p style={{marginTop:22,color:'blue'}}>{ member.email} </p>
                        <div className="article-content">
                                    
                                        
                                    <h3>{member.title}</h3>
  
  
                                    <div className="content" dangerouslySetInnerHTML={{__html: member.details}}></div>
                                
                                   
                                  </div>

                            <div className="post-controls-buttons">
                            {this.state.alert}
                            
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}


export default TeamMemberBody;
