import { Redirect} from 'react-router-dom';
import Form from './Form';

class DetailsBody extends Component {

    constructor(props) {
        super(props);
        this.state = {
            course: {
                open:false,
                description:"",
                details:"",
                loading:false,
                alert: null,
                redirect:null,
            },
            token: localStorage.getItem('token',null),
        }
    }


    getinit = (id) => {
        // this.setState({
        //     loading:true
        // })
        // const url = 'https://api.venture-consult.com/courses/'+id;   // site that doesn’t send Access-Control-*
        // const proxyurl = "https://cors-anywhere.herokuapp.com/";
          
        // fetch(  url, {
        //     method: 'GET',
        //     headers:
        //     {
        //         'Access-Control-Allow-Origin': '*',
        //         'Access-Control-Allow-Credentials': 'true',
        //         'Accept': 'application/json',
        //         'Content-Type': 'application/json',
        //     },

        // })
        //     .then((response) => response.json()).then((data) => {
        //         if (data.id > 0)
        //             this.setState({
        //                 course: data,
        //                 loading:false
        //             })
        //         else {
        //             this.setState({
                        
        //                 loading:false
        //             })
        //         }

        //     }).catch((e) => {
        //         console.log(e);
        //         this.setState({
                    
        //             loading:false
        //         })
        //     });
        const { course } = this.props;
        this.setState({
            course
        })

    }
    
    componentDidMount() {
        
        const { id } = this.props;
        
        this.getinit(id);
    
     
    }

    toLogin = () => {
       const Login = () => <Redirect to="/Login" />
       this.setState({
           redirect : Login
       })
    }

    onAttend= () => {
        this.setState({
            loading:true
        })
        const { id } = this.props ;
        const { token } = this.state;
        const url = 'https://api.venture-consult.com/users/courses/'+id;   // site that doesn’t send Access-Control-*
        const proxyurl = "https://cors-anywhere.herokuapp.com/";
          
        fetch(  url, {
            method: 'POST',
            headers:
            {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Credentials': 'true',
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                token,
            })

        })
            .then((response) => response.json()).then((data) => {
              // console.log("RES",data);
                this.setState({ 
                    loading:false,
                    alert: null
                });
                 alert(data.message);
            }).catch((e) => {
                console.log(e);
             //   alert("PLEASE TRY AGAIN")
                this.setState({ 
                    loading:false,
                    alert: null
                })
            });
    }
    onConfirm= () => {
        this.onAttend();
    }
    onCancel = () => {
        this.setState({
            alert:null
        })
    }
    onRegister = (e) => {
        e.preventDefault();
    
        if ( !! this.state.token ){
           const getAlert = () => (
                <SweetAlert
                Primary
                showCancel
                confirmBtnText="Confirm"
              
                title="Are you sure ?"
                onConfirm={this.onConfirm}
                onCancel={this.onCancel}
                focusCancelBtn
               >
                
              </SweetAlert>
            );
            this.setState({
                alert: getAlert()
            });
        } else {
            const getAlert = () => (
                <SweetAlert
                Primary
                showCancel
                confirmBtnText="Login"
              
                title="You are not logged in!"
                onConfirm={this.toLogin}
                onCancel={this.onCancel}
                focusCancelBtn
               >
                Please log in to continue.
              </SweetAlert>
            );
            this.setState({
                alert: getAlert()
            });
        }
        
    
        
      
    }

    render() {
        if ( this.state.loading ){
            return ( <div className="loader ptb-100"> <Loader type="BallTriangle" color="blue" height={40} width={40} /> </div>);
        }
        const { course  } = this.state ;
        return (
            <section className="blog-details-area loader ptb-100" >
                <div className="container" >
                    <div className="row h-100 justify-content-center align-items-center">
                        <div className="col-lg-8">
                            <div className="blog-details">
                                <div className="article-img">
                                    <img src={`${course.path}`}  alt="blog-details" />
                                     <div className="date" style={{backgroundColor: ( course.open ? '#3385ff': 'red' )}}> { course.open ? "open" : "closed"}</div>
                                </div>
                                
                                <div className="article-content">
                                    
                                        
                                  <h3>{course.description}</h3>


                                  <div className="content" dangerouslySetInnerHTML={{__html: course.details}}></div>
                                    
                                 
                                </div>
                            </div>
                            <div className="post-controls-buttons">
                            {this.state.alert}
                            {this.state.redirect}

                              { (!!course.open)  &&  
                                <div className="loader">
                                     <Form id={this.props.id} />
                                </div>
                                }      
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

const mapStateToProps = state =>({
    isLogin : state.isLogin, 
})
export default connect ( mapStateToProps , {} ) (DetailsBody);
