import React, { Component } from 'react';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import { Link } from 'react-router-dom';
import Loader from 'react-loader-spinner';

const images = [
    require('../../images/7.jpg'),
    require('../../images/10.jpg'),
    require('../../images/10.jpg'),
    require('../../images/7.jpg'),
    require('../../images/10.jpg'),
    require('../../images/10.jpg'),
    require('../../images/7.jpg'),
    require('../../images/7.jpg')
]

class GalleryContent extends Component {

    constructor(props) {
        super(props);
        this.state = {
            arr:images,
            display: false,
            photoIndex: 0,
            isOpenImage: false,
            cur: "",
            loading: false,
            page: 1,
            hasNext:true,
        }
    }

    getinit = (page=1) => {
        this.setState({ loading: true });
        
      //  console.log("PAGE", page);
        
        const url = `https://api.venture-consult.com/training/all?page=${page}`;   // site that doesn’t send Access-Control-*
        const proxyurl = "https://cors-anywhere.herokuapp.com/";

        fetch(url, {
            method: 'GET',
            headers:
            {
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Credentials': 'true',
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },

        })
            .then((response) => response.json()).then((data) => {
               // console.log("RES",data.data);
                
                if (!!data.data && data.data.length > 0)
                    this.setState({
                        arr: data.data,
                        loading: false,
                        hasNext: !!data.next_page_url,
                    });

            }).catch((e) => {
               // console.log(e);
                this.setState({ loading: false });
            });

    }

    nextpage = () => {
        const { page } = this.state;
        this.setState( (prevState)=>({page:prevState.page+1}) , this.getinit(page+1));
     
    }
    prevpage = () => {
        const { page } = this.state;
        this.setState( (prevState)=>({page:prevState.page-1}) , this.getinit(page-1));
    }

    componentDidMount() {
     //   this.getinit();
        this.setState({ display: true })
    }

    render() {
        if (this.state.loading) {
            return (<div className="loader ptb-100"> <Loader type="BallTriangle" color="blue" height={40} width={40} /> </div>);
        }
        const { photoIndex, isOpenImage , page , hasNext } = this.state;

       // console.log("ARR",this.state.arr);
        
        return (
            <section className="gallery-area ptb-100">
                <div className="row m-0">

                    {this.state.arr.map((img) => (
                        <div key={img.id} className="col-lg-3 col-md-6" style={{ marginBottom: 22 }}>
                            <div className="single-image">
                                <img style={{ width: 400, height: 175 }} src={img} alt="gallery" />

                                <Link href="#">
                                    <a
                                        className="popup-btn"
                                        onClick={e => { e.preventDefault(); this.setState({ isOpenImage: true, cur: img }) }}
                                    >
                                        <i className="icofont-plus"></i>
                                    </a>
                                </Link>
                            </div>
                        </div>

                    ))}

                    <div className="col-lg-12 col-md-12">
                        <div className="pagination-area">
                            <nav aria-label="Page navigation example">
                                <ul className="pagination justify-content-center">

                    { page > 1 &&   <li className="page-item"><a className="page-link" onClick={()=> this.prevpage()} ><i className="icofont-double-left"></i></a></li> }

                                    <li className="page-item active"><a className="page-link"  >{page}</a></li>

                    {  hasNext &&   <li className="page-item"><a className="page-link" onClick={()=> this.nextpage()} ><i className="icofont-double-right"></i></a></li> }
                                </ul>
                            </nav>
                        </div>
                    </div>

                    {isOpenImage && (
                        <Lightbox
                            mainSrc={`${this.state.cur}`}
                            // nextSrc={images[(photoIndex + 1) % images.length]}
                            // prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                            onCloseRequest={() => this.setState({ isOpenImage: false })}
                            onMovePrevRequest={() =>
                                this.setState({
                                    photoIndex: (photoIndex + images.length - 1) % images.length,
                                })
                            }
                            onMoveNextRequest={() =>
                                this.setState({
                                    photoIndex: (photoIndex + 1) % images.length,
                                })
                            }
                        />
                    )}


                </div>
            </section>
        );
    }
}

export default GalleryContent;
