import React from 'react';

import OwlCarousel from 'react-owl-carousel3';

const options = {
    autoplay: true,
    nav: false,
    mouseDrag: true,
    autoplayHoverPause: true,
    responsiveClass: true,
    dots: false,
    loop: true,
    navText: [
    "<i class='icofont-bubble-left'></i>",
    "<i class='icofont-bubble-right'></i>"
    ],
    responsive:{
        0:{
            items:3,
        },
        768:{
            items:4,
        },
        1024:{
            items:5,
        },
        1200:{
            items:6,
        }
    }
}

class Partner extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        arr:null,
        display:false,
        photoIndex: 0,
        isOpenImage: false,
    }
}

getinit = () => {

    const url = 'https://api.venture-consult.com/partners/all';   // site that doesn’t send Access-Control-*
      const proxyurl = "https://cors-anywhere.herokuapp.com/";
  
      fetch(proxyurl+url,{
          method: 'GET',
          headers:
          {
              'Access-Control-Allow-Origin':'*',
              'Access-Control-Allow-Credentials': 'true',
              'Accept': 'application/json',
              'Content-Type': 'application/json',
          },
    
      })
      .then((response) => response.json()).then( (data) =>
      {   
          if ( data.length > 0 ) 
          this.setState({
              arr: data
          })

      }).catch((e) =>
      {
        console.log(e);
      });

 }
  componentDidMount(){
      this.getinit();
        this.setState({ display: true }) 
    }
    render() {
        return (
            <div className="partner-area marketing-partner">
                <div className="container">
                <div className="container">
                    <div className="section-title">
                        <h2>Partners</h2>
                        <div className="bar"></div>
                     </div>
                </div>
                    <div className="row">
                        {(!!this.state.arr)? <OwlCarousel 
                            className="partner-slides owl-carousel owl-theme"
                            {...options}
                        >
                           { this.state.arr.map( (partner) => (
                            <div key={partner.id} className="col-lg-12">
                                <div className="item">
                                        <a><img src={`${partner.logo}`} alt="partner" /></a>
                                </div>
                            </div>
                            ) )}

                            {/* <div className="col-lg-12">
                                <div className="item">
                                    <Link href="#">
                                        <a><img src={require('../../images/partner-2.png')} alt="partner" /></a>
                                    </Link>
                                </div>
                            </div>

                            <div className="col-lg-12">
                                <div className="item">
                                    <Link href="#">
                                        <a><img src={require('../../images/partner-3.jpeg')} alt="partner" /></a>
                                    </Link>
                                </div>
                            </div>

                            <div className="col-lg-12">
                                <div className="item">
                                    <Link href="#">
                                        <a><img src={require('../../images/partner-4.png')} alt="partner" /></a>
                                    </Link>
                                </div>
                            </div> */}

                            
                        </OwlCarousel> : ''}
                    </div>
                </div>
            </div>
        );
    }
}

export default Partner;
