import React from 'react';
import { BrowserRouter as Router, Link, Route, Switch, Redirect } from 'react-router-dom';

import Home from '../pages/Home';
import Blog from '../pages/Blog';
import Gallery from '../pages/Gallery';
import About from '../pages/About';
import Faq from '../pages/Faq';
import Details from '../pages/Details';
import MyCourses from '../pages/MyCourses';
import Verify from '../pages/Verify';
import Terms from '../pages/Terms';
import TeamMeber from '../pages/TeamMember';
import NGO from '../pages/NGO';

export default class App extends React.Component {

  render() {
    return (
      <Router>
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/Courses" exact component={Blog} />
          <Route path="/Gallery" exact component={Gallery} />
          <Route path="/About" exact component={About} />
          {/* <Route path="/Login" exact component={Login} /> */}
          {/* <Route path="/Signup" exact component={Signup} />*/}
          <Route path="/PrivacyPolicy" exact component={Verify} /> 
          <Route path="/TermsOfService " exact component={Terms} /> 
          <Route path="/Faq" exact component={Faq} />
          <Route path="/NGO" exact component={NGO} />
          {/* <Route path="/Profile" exact component={Profile} /> */}
          {/* <Route exact path="/MyCourses/:id" render={(props) => <MyCourses  {...props} />} /> */}
          <Route exact path="/details/:id" render={(props) => <Details  {...props} />} />
          <Route exact path="/Team/:id" render={(props) => <TeamMeber  {...props} />} />
          <Route path='/Facebook' component={() => {
            window.location.href = 'https://www.facebook.com/VentureIlc/';
            return null;
          }} />
          <Route path='/Twitter' component={() => {
            window.location.href = 'https://twitter.com/venture_intl';
            return null;
          }} />
          <Route path='/Instagram' component={() => {
            window.location.href = 'https://www.instagram.com/venture_llc/';
            return null;
          }} />
          <Redirect to="/" />
        </Switch>
      </Router>
    );
  }
}
