

import  * as actions  from '../actions/action-types/cart-actions'

const initState = {
  
    isLogin: false , message: '' ,isRegister : false ,registerMessage: '' , user_name: '',
    loading:false,
    editMsg:'',
    editLoading:false,

}

export default (state = initState, action) => {
    
    switch (action.type) {
        case actions.LOGINGIN :
        return {...state, isLogin : true , message : '' , user_name: action.user_name , loading:false }
        case actions.LOGIN_FAIL:
        return {...state, isLogin : false , message : action.payload , userPhone: action.phone , user_name:'',loading:false }
        case actions.LOGINGOUT :
        return {...state, isLogin : false , message : ''  , registerMessage : '' , isRegister : false , user_name :'' }
        case actions.REGISTERING:
        return {...state, isRegister : true  , isLogin: false , message:'' ,registerMessage:action.payload , loading:false}
        case actions.REGISTER_FAIL:
        return {...state, isRegister : false ,isLogin:false , registerMessage : action.payload , user_name: '',loading:false}
        case actions.EDITED:
        return {...state,  editMsg:action.payload , editLoading:false}
        case actions.EDIT_FAIL:
        return {...state, editMsg:action.payload , editLoading:false}
        case actions.CLEAR_MSG:
        return {...state,message:'',editMsg:'', registerMessage:''}
        case action.EDIT:
            return { ...state , editLoading: true }
        case actions.LOADING:
            return{...state,message:'',loading:true}
        default:
            return state ;
    }
}
   
